@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@400;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Archivo+Black&display=swap);
body { /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #667db6, #0082c8, #0082c8, #667db6); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.headers {
  text-align:center;
  margin: 30px 0 !important;
}

.subheading {
  color:white
}
.App { /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #667db6, #0082c8, #0082c8, #667db6); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  display:flex;
  flex-flow: column;
  font-family: 'Source Sans Pro', sans-serif;

}

.body {
  flex: 2 1;
  overflow:auto;
}
.navbar {
    background-color: rgba(12,84,122,0.8);
}

.navbar-brand {
    color:white !important;
    font-family: 'Archivo Black', sans-serif;
}

.navbar-brand span {
    color: #C71420 !important;
}

.nav-item a {
    margin: 0 15px;
    color:white;
  
}

.nav-item a:hover {
    background:white;
    color:#0082c8;
    text-decoration: none;
    padding:5px;
    border-radius:10px;
}



.aboutCard {
    margin:auto auto 30px auto !important;
}

.skillIcons svg, .skill-icons p svg  {
    font-size:50px;
    margin: 10px;
}

.cardHeader {
    margin-bottom:20px;
}
.container {
  margin-bottom: 100px !important;
}

.row {
  width: 100%;
  margin: auto !important;
  padding: 0;
}

.card {
  display: flex;
  align-items: center;
  margin: 5px;
}

.screenshot {
  height: 20em;
  width: auto;
  object-fit: cover;
  border: 1px black solid;
  margin-top: 5px;
}

.projectBtn {
  margin: 10px;
  font-size: 18px;
}

.projectBtn button {
  border-radius: 10%;
  background-color: #0082c8;
  color: white;
  padding: 5px 10px;
  transition: all 1s ease 0s
}

.homeBackground{
    display: flex;
    align-items:center;
    justify-content: center;
}

.homeTexts {
    text-align:center;
    position:absolute;
    line-height:30px;
    top:40%;
}

.homeTexts h1 {
    font-size:70px;
}

.homeTexts h4 {
    margin: 20px 0;
}

.sns-icon {
    margin:10px;
    background-color:rgba(12,84,122,0.8);
    font-size:40px;
    line-height:40px;
    height:5rem;
    width:5rem;
    border-radius: 50%;
    box-shadow:5 5px 5px 5 rgba(0, 0, 0, 0.9);
    color:white;
}

.sns-icon:hover {
    background-color: #C71420;
}
footer {
    height:30px;
    line-height:30px;
    background-color: rgba(12,84,122,0.8);
    text-align:center;
    position:fixed;
    bottom:0;
    width:100%;
    color:white;

}
